.popup__input {
  width: 100%;
  margin-top: 30px;
  outline: 0;
  border-width: 0 0 1px;
  border-color: rgba(0, 0, 0, 0.2);
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  padding: 0;
  padding-bottom: 10px;
  display: block;
}

.popup__input:first-of-type {
  margin-top: 0px;
}


@media (max-width: 320px) {
  .popup__input {
    padding-bottom: 8px;
  }
  /*.popup__input:last-of-type {
    margin-bottom: 45px;
  }*/
}
