.popup__close {
  background-image: url('../../../images/popup/CloseIcon.svg');
  background-repeat: no-repeat;
  background-color: transparent;
  background-size: contain;
  border: none;
  width: 32px;
  height: 32px;
  position: absolute;
  right: -40px;
  top: -40px;
  cursor: pointer;
  transition: opacity .4s;
}

.popup__close:hover {
  opacity: 0.6;
}

@media (max-width: 550px) {
  .popup__close {
    right: 0px;
    top: -38px;
    width: 20px;
    height: 20px;
  }
}